<template>
  <div class="main">
    <div class="columns">
      <div class="column box is-one-third">        
        <img src="data:image/jpeg;base64,">
        <h2 class="title is-3">Quels traits de caractère te définissent le mieux ?</h2>
          <div class="column">
            <b-field label="Ta principale qualité ?">
              <b-autocomplete
                rounded
                v-model="name"
                :data="filteredDataArrayQuality"
                placeholder="Écris si tu oses"
                @select="optionQuality => qualitySelected = optionQuality">
                <template slot="empty">Je l'ai pas celui là..</template>
              </b-autocomplete>
            </b-field>
            <b-field>
              <b-input  v-if="name" rounded placeholder="Un indice peut être ?" v-model="qualityHint" type="text"></b-input>
            </b-field>
          </div>  
          <div class="column">
            <b-field label="Ton principal défaut ?">
              <b-autocomplete
                rounded
                v-model="name2"
                :data="filteredDataArrayDefect"
                placeholder="On en a tous un petit..."
                @select="optionDefect => defectSelected = optionDefect">
                <template slot="empty">Je l'ai pas celui là..</template>
              </b-autocomplete>
            </b-field>
            <b-field>
              <b-input v-if="name2" rounded placeholder="Un indice peut être ?" v-model="defectHint" type="text"></b-input>
            </b-field>
          </div>
        <div>
          <button class="button is-danger" @click.prevent="sendTraits" >Finaliser</button>
        </div>
      </div>
    </div> 
  </div>
</template>

<script>
  import quality from "../quality";
  import defect from "../defect";
  import api from "../api";

  export default {
    data() {
      return {
        name: "",
        name2: "",
        quality: quality,
        qualityHint: "",
        qualitySelected: null,
        defect: defect,
        defectHint: "",
        defectSelected: null
      };
    },
    methods: {
      sendTraits() {
        api
          .sendTraits({
            userId: localStorage.getItem("id"),
            traits: {
              quality: {
                answer: this.name,
                hint: this.qualityHint
              },
              defect: {
                answer: this.name2,
                hint: this.defectHint
              }
            }
          })
          .then(data => {
            this.$toast.open({
              message: `C'est parti!`,
              type: "is-success"
            });
            this.$router.push("/swiper");
          })
          .catch(err => {
            this.error = err;
          });
      }
    },
    computed: {
      filteredDataArrayQuality() {
        return this.quality.filter(option => {
          return (
            option
              .toString()
              .toLowerCase()
              .indexOf(this.name.toLowerCase()) >= 0
          );
        });
      },
      filteredDataArrayDefect() {
        return this.defect.filter(option => {
          return (
            option
              .toString()
              .toLowerCase()
              .indexOf(this.name2.toLowerCase()) >= 0
          );
        });
      }
    }
  };
</script>

<style>
  img {
    margin-top: 12px;
    width: 90px;
  }

  .box {
    margin: 0 auto;
    margin-top: 5vh;
  }

  .main {
    background-image: url("http://mtmt.viviansarazin.com/images/traits.jpeg");
    background-attachment: fixed;
    background-position: center center;
    background-size: cover;
    height: 400vh;
  }
</style>


